import Vue from 'vue'
import Router from 'vue-router'

import home from './components/home'
import list from  "./components/list"
import video from "./components/video"
import search from "./components/search"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: home
    },
    // show/showname/season/episode/quality
    {
        path:"/series/:showname/:season?/:episode?/",
        name:"series",
        props:true,
        component:list,
    },
    {
        path:"/video/:showname/:season/:episode/:quality/",
        name:"video",
        props:true,
        component:video,
    },
    {
      path:"/show/:showname/",
      name:"show",
      props:true,
      component:search,
    },
  ]
})