<template>
    <v-container>
        <v-list transition="slide-x-transition">
            <v-list-tile v-for="(i,index) in list" :key="index" @click="gonext(i)">
                <v-list-tile-title :key="index">{{i}}</v-list-tile-title>
                <v-divider></v-divider>
            </v-list-tile>
            <v-list-tile v-if="!list.length">
                <v-list-tile-title>Cannot Find!</v-list-tile-title>
            </v-list-tile>
            
        </v-list>
    </v-container>
</template>

<script>
import axios from "axios"
export default {
    props:["showname","season","episode"],
    data:()=>({
        list:[],
    }),
    beforeRouteEnter (to, from, next) {
        var addr=window.api+"/series";
        if(to.params.showname){
            addr+="/"+to.params.showname;
        }
        if(to.params.season){
            addr+="/"+to.params.season;
        }
            
        if(to.params.episode){
            addr+="/"+to.params.episode;
        }
        axios.get(addr,window.config).then((res)=>{
            next((vm)=>{vm.setData(res);});
        })
    },
  beforeRouteUpdate (to, from, next){
        var addr=window.api+"/series";
        if(to.params.showname){
            addr+="/"+to.params.showname;
        }
        if(to.params.season){
            addr+="/"+to.params.season;
        }
            
        if(to.params.episode){
            addr+="/"+to.params.episode;
        }
        axios.get(addr,window.config).then((res)=>{
            this.setData(res);
            next();
        });
  },
  methods :{
      setData(res)
      {
          this.list=res.data;
      },
      gonext(i)
      {
          if(this.episode)
          {
              //quality
              this.$router.push({name:"video",params:{showname:this.showname,season:this.season,episode:this.episode,quality:i}})
          }
          else if(this.season)
          {
              //episode
              this.$router.push({name:'series',params:{showname:this.showname,season:this.season,episode:i}});
          }
          else if(this.showname)
          {
              //season
              this.$router.push({name:'series',params:{showname:this.showname,season:i}});
          }
      },
  },
}
</script>

