<template>
    <v-container>
        <v-list>
            <v-list-tile v-for="(i,index) in list" :key="index" @click="goseries(i)">
                <v-list-tile-title :key="index">{{i}}</v-list-tile-title>
                <v-divider></v-divider>
            </v-list-tile>
            <v-list-tile v-if="!list.length">
                <v-list-tile-title>Cannot Find!</v-list-tile-title>
            </v-list-tile>
            
        </v-list>
    </v-container>
</template>

<script>
import axios from "axios"
export default {
    props:["showname"],
    data:()=>({
        list:[],
    }),
    beforeRouteEnter (to, from, next) {
        var addr=window.api+"/show";
        var data=new FormData();
        data.append("q",to.params.showname);
        axios.post(addr,data,window.config).then((res)=>{
            next((vm)=>{vm.setData(res);});
        })
    },
  beforeRouteUpdate (to, from, next){
        var addr=window.api+"/show";
        var data=new FormData();
        data.append("q",to.params.showname);
        axios.post(addr,data,window.config).then((res)=>{
            this.setData(res);
            next();
        });
  },
  methods :{
      setData(res)
      {
          this.list=res.data;
      },
      goseries(i)
      {
          this.$router.push({name:'series',params:{showname:i}});
      }
  },
}
</script>

