<template>
    <v-container>
        <video controls class="player">
            <source v-for="(i,index) in list" :key="index" :src="i"/>
        </video>
        <v-list>
            <v-list-tile v-for="(i,index) in list" :key="index" @click="">
                <v-list-tile-title :key="index"><a :href="i">{{i}}</a></v-list-tile-title>
                <v-divider></v-divider>
            </v-list-tile>
            <v-list-tile v-if="!list.length">
                <v-list-tile-title>Cannot Find!</v-list-tile-title>
            </v-list-tile>
        </v-list>
    </v-container>
</template>

<script>
import axios from "axios"
export default {
    props:["showname","episode","season","quality"],
    data:()=>({
        list:[],
    }),
    beforeRouteEnter (to, from, next) {
        var addr=window.api+"/series"+"/"+to.params.showname+"/"+to.params.season+"/"+to.params.episode+"/"+to.params.quality;
        axios.get(addr,window.config).then((res)=>{
            next((vm)=>{vm.setData(res);});
        })
    },
  beforeRouteUpdate (to, from, next){
        var addr=window.api+"/series"+"/"+to.params.showname+"/"+to.params.season+"/"+to.params.episode+"/"+to.params.quality;
        axios.get(addr,window.config).then((res)=>{
            this.setData(res);
            next();
        });
  },
  methods :{
      setData(res)
      {
          this.list=res.data;
      },
  },
}
</script>

<style>
.player {
    width:100%;
}
</style>
